<template>
    <el-dialog title="用户扩展信息" :visible.sync="dialogForm" class="updatePwd">
        <el-form label-position="left" inline class="demo-table-expand" v-for="item in list" :key="item.name">
            <el-form-item :label="item.name" >
                <span>{{ item.data }}</span>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">关闭</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:0 30px;
        }
    }
</style>

<script>
    import QuRequest from '../../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                dialogForm: false,
                list: [],
                form: {
                    member_id: ''
                }
            }
        },
        methods: {
            showDialog(id) {
                if(id){
                    this.form.member_id = id;
                } else {
                    this.form.member_id = '';
                }
                this.dialogForm = true;
                this.getList();
            },
            getList(){
                this.loading = true;
                let { form } = this;
                quRequest.send(quRequest.Apis.qutuiba.member.extendInfo,form
                ).then(res => {
                    let data = res.data;
                    this.list = data || [];
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
            },
            submitPwd(){
                quRequest.send(quRequest.Apis.qutuiba.member.modifyPassword,this.updatePwd)
                    .then(res => {
                        this.updatePwd.password = '';
                        this.dialogForm = false;
                        this.$message.success('修改密碼成功！');
                    });
            }
        }
    }
</script>


